<template>
    <el-dialog
    width="600px"
    :title="(isCreate ? '新增' : '编辑') + '用户'"
    :visible.sync="isVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    @close="cancel"
    >
       <el-form ref="roleForm" :model="roleForm" :rules="formRules" label-width="80px">
           <el-row>
               <el-col :span="12">
                    <el-form-item label="角色名称" prop="roleName">
                        <el-input v-model="roleForm.roleName" />
                    </el-form-item>
               </el-col>
           </el-row>
         <el-row>
           <el-col :span="12">
             <el-form-item label="类型" prop="roleEnable">
               <el-select v-model="roleForm.roleType" placeholder="请选择角色类型">
                 <el-option
                   v-for="(item,index) in this.roleType"
                   :key="index"
                   :label="item.label"
                   :value="item.value">
                 </el-option>
               </el-select>
             </el-form-item>
           </el-col>
               <el-col :span="12">
                   <el-form-item label="状态" prop="roleEnable">
                        <el-radio-group v-model="roleForm.roleEnable">
                            <el-radio :label=1>启用</el-radio>
                            <el-radio :label=0>禁用</el-radio>
                        </el-radio-group>
                    </el-form-item>
               </el-col>
           </el-row>
           <el-row>
               <el-col :span="24">
                   <el-form-item label="角色描述" prop="roleRemark">
                    <el-input type="textarea" :rows="2" v-model="roleForm.roleRemark"/>
                   </el-form-item>
               </el-col>

           </el-row>
            <el-row>
                <el-col :span="6" :offset="18">
                    <el-button type="primary" @click="save">保存</el-button>
                    <el-button @click="cancel">取消</el-button>
                </el-col>
            </el-row>
       </el-form>
    </el-dialog>
</template>
<script>
/**
 * Roles Add/Edit Component
 * ----------------------
 * Author: zhou qi
 * Date: 2018.03.20
 */
// api
import { createRole, updateRole } from '@/api/role'

//
const initRolesForm = {
  roleName: '',
  roleEnable: '1',
  roleRemark: ''
}

export default {
  name: 'RoleUpdate',
  data () {
    return {
      isVisible: false,
      roleForm: Object.assign({}, initRolesForm),
      formRules: {
        roleName: [{required: true, message: '请输入角色名称', trigger: 'blur'}],
        roleEnable: [{required: true, message: '请选择角色类型', trigger: 'blur'}]
      },
      roleType: [
        {
          value: "0",
          label: "系统管理员"
        },
        {
          value: "1",
          label: "商户"
        },
        {
          value: "2",
          label: "仓库管理员"
        },
        {
          value: "3",
          label: "财务管理"
        },
        {
          value: "4",
          label: "招商管理"
        }
      ]
    }
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    roleData: {
      type: Object
    },
    isCreate: {
      type: Boolean,
      required: true
    }
  },
  watch: {
    show (isShow) {
      if (isShow) {
        if (this.isCreate) {
          this.roleForm = Object.assign({}, initRolesForm)
        } else {
          this.roleForm = Object.assign({}, this.roleData)
        }
      }
      //
      this.isVisible = isShow
    }
  },
  methods: {
    save () {
      this.$refs.roleForm.validate(isValid => {
        if (isValid) {
          const params = Object.assign({}, this.roleForm)
          const submitType = this.isCreate ? createRole : updateRole
          const submitText = this.isCreate ? '新增' : '编辑'
          submitType(params).then(res => {
            this.$message({
              message: `${submitText}角色成功!`,
              type: 'success'
            })
            this.cancel()
            this.$emit('updateList')
          })
        }
      })
    },
    cancel () {
      this.roleForm = Object.assign({}, initRolesForm)
      this.$refs.roleForm.resetFields()
      this.$emit('update:show', false)
      this.$emit('update:roleData', Object.create(null))
      this.isVisible = false
    }
  }
}
</script>



